<template>
  <div>
    <router-link tag="div" to="/" class="header-back" v-show="!showTop">
      <span class="iconfont back-icon">&#xe624;</span>
    </router-link>
    <div :style="OpacityStyle" tag="div" to="/" class="header-top" v-show="showTop">
      <router-link tag="div" to="/" class="iconfont top-back-icon">&#xe624;</router-link>
      <p class="top-title">北京野生动物园</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showTop: false,
      OpacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScoll () {
      let top = document.documentElement.scrollTop
      if (top > 15) {
        let opacity = (top / 2) / 80
        opacity = opacity > 1 ? 1 : opacity
        this.OpacityStyle = {
          opacity: opacity
        }
        this.showTop = true
      } else {
        this.showTop = false
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScoll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScoll)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header-back
    position absolute
    top .1rem
    left .1rem
    width .72rem
    height .72rem
    line-height .72rem
    border-radius 50%
    text-align center
    color #fff
    background-color rgba(0,0,0,.7)
  .header-top
    position fixed
    z-index 2
    top 0
    left 0
    right 0
    height .9rem
    background-color $bgColor
    text-align center
    .top-back-icon
      position absolute
      color #fff
      line-height .9rem
      text-align center
      padding 0 .2rem
    .top-title
      font-size .32rem
      color #ffffff
      line-height .9rem
</style>



