<template>
  <div>
     <div class="title">周末去哪儿</div>
     <ul>
       <li class="item border-bottom" v-for="item of list" :key="item.id">
         <div class="item-img-wrapper">
           <img class="item-img" :src="item.imgUrl">
         </div>
         <div class="item-info">
           <p class="item-title">{{item.title}}</p>
           <p class="item-desc">{{item.desc}}</p>
         </div>
       </li>
     </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .title
    line-height .8rem
    text-indent .2rem
    background-color #eee
  .item
    .item-img-wrapper
      overflow hidden
      height 0
      padding-bottom 37.1%
      .item-img
        width 100%
    .item-info
      padding .2rem
      .item-title
        font-size .28rem
        line-height .54rem
        ellipsis()
      .item-desc
        font-size: .24rem
        line-height .4rem
        color #616161
        ellipsis()
</style>



