<template>
  <div class="list">
    <div class="list-wrapper border-bottom" v-for="(item, index) of list" :key="index">
      <span class="ticket"></span>
      <div class="list-item">{{item.title}}</div>
      <div v-if="item.children">
        <detail-list :list="item.children"></detail-list>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailList',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  .list-wrapper
    overflow hidden
    line-height .88rem
    font-size .32rem
    padding-left .6rem
    .ticket
      position absolute
      width .36rem
      height .36rem
      top .26rem
      left .2rem
      background: url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat
      margin-right .1rem
      background-size .4rem 3rem
</style>



