<template>
  <div>
     <div class="title">热销推荐</div>
     <ul>
       <router-link class="item border-bottom"
       tag="li"
       v-for="item of list"
       :key="item.id"
       :to="'/detail/' + item.id">
         <img class="item-img" :src="item.imgUrl">
         <div class="item-info">
           <p class="item-title">{{item.title}}</p>
           <p class="item-desc">{{item.desc}}</p>
           <button class="item-button">查看详情</button>
         </div>
       </router-link>
     </ul>
  </div>
</template>

<script>
export default {
  name: 'Recommend',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .title
    margin-top .2rem
    line-height .8rem
    text-indent .2rem
    background-color #eee
  .item
    overflow hidden
    display flex
    height 2.2rem
    .item-img
      width 2rem
      height 1.9rem
      padding .2rem
    .item-info
      flex 1
      padding .2rem
      min-width 0
      .item-title
        font-size .32rem
        line-height .54rem
        ellipsis()
      .item-desc
        line-height .4rem
        margin-top .1rem
        color #ccc
        ellipsis()
      .item-button
        line-height .44rem
        background-color #ff9300
        padding 0 .1rem
        margin-top .3rem
        color #fff
        border-radius .06rem
</style>



