<template>
  <div>
    <div class="banner-warpper" @click="handleBannerClick">
      <img class="image" :src="bannerImg">
      <div class="banner-info">
        <p class="title">{{this.sightName}}</p>
        <div class="number">
          <span class="iconfont banner-icon">&#xe678;</span>
          {{this.gallaryImgs.length}}
        </div>
      </div>
    </div>
    <fade-animation>
      <common-gallay @close="handleGallaryClose" :gallaryImgs="gallaryImgs" v-show="showGallary"></common-gallay>
    </fade-animation>
  </div>
</template>

<script>
import CommonGallay from 'common/gallary/Gallary.vue'
import FadeAnimation from 'common/fade/FadeAnimation.vue'
export default {
  name: 'DetailBanner',
  props: {
    sightName: String,
    bannerImg: String,
    gallaryImgs: Array
  },
  data () {
    return {
      showGallary: false
    }
  },
  methods: {
    handleBannerClick () {
      this.showGallary = true
    },
    handleGallaryClose () {
      this.showGallary = false
    }
  },
  components: {
    CommonGallay: CommonGallay,
    FadeAnimation: FadeAnimation
  }
}
</script>

<style lang="stylus" scoped>
  .banner-warpper
    position relative
    overflow hidden
    height 0
    padding-bottom 55%
    .image
      width 100%
    .banner-info
      display flex
      justify-content space-between
      position absolute
      left 0
      right 0
      bottom 0
      height .6rem
      padding 0 .2rem
      background-image linear-gradient(top, rgba(0,0,0, 0), rgba(0, 0, 0, .8))
      .title
        flex 1
        line-height .4rem
        font-size .36rem
        color #ffffff
      .number
        height .4rem
        line-height .4rem
        padding 0 .4rem
        font-size .24rem
        background-color rgba(0,0,0, .5)
        border-radius .2rem
        color #ffffff
        .banner-icon
          font-size .24rem
</style>



