<template>
  <div class="gallary" @click="handleGallayClick">
    <div class="gallary-wrapper">
      <swiper :options="swiperOption">
        <swiper-slide v-for="(item, index) of gallaryImgs" :key="index">
            <img class="gallary-img" :src="item">
        </swiper-slide>
        <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallay',
  props: {
    gallaryImgs: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        observeParents: true,
        observer: true
      }
    }
  },
  methods: {
    handleGallayClick () {
      this.$emit('close')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .gallary >>> .swiper-container
    overflow inherit
  .gallary
    display flex
    flex-direction column
    justify-content center
    position fixed
    top 0
    left 0
    right 0
    bottom 0
    z-index 99
    background-color #000
    .gallary-wrapper
      height 0
      padding-bottom 66.66%
      background-color #fff
      .gallary-img
        width 7.5rem
        height 5rem
      .swiper-pagination
        color #fff
        bottom -1rem
</style>



